@import "../variables";

.about {
    padding: 120px 50px 10px 50px;
    background-color: $black;
    color: white;

    h1{
        font-size: 2.25em;
        margin-bottom: 40px;
    }

    p{
        font-size: 1.5em;
        line-height: 1.5em;
        padding-bottom: 20px;
    }
  
    .about-links{
        margin-top: 35px;

        a{
            font-family: 'Kollektif';
            letter-spacing: .1em;
            color: $green;
            display: block;
            width: -webkit-fit-content;
            width: -moz-fit-content;
            width: fit-content;
            margin: 10px 0;
        }
    }
}
