@import '../variables';

.project{
    display: block;
    margin-bottom: 40px;
    padding: 20px 30px;
    border: 1px $black solid;
    border-radius: 5px;

    .header{
        display: flex;
        justify-content: space-between;

        h2{
            font-size: 1.5em;
        }

        #project-arrow{
            width: 30px;
            transition: transform .25s ease;
        }

        .flip{
            transform: rotateZ(180deg);
        }
    }

    .description{
        max-height: 0;
        overflow: hidden;
        transition: max-height .25s ease;
        will-change: max-height;
        
        ul{
            display: flex;
            margin: 15px 0 10px 0;
    
            li{
                display: unset;
                margin-right: 10px;
                padding: 7.5px 10px; 
                background-color: $light-green;
                border-radius: 7.5px;
                font-size: 1em;
                font-family: "Kollektif";
                color: $light-black;
            }
        }

        p{
            margin: 20px 50px 20px 0;
            font-size: 1.25em;
            line-height: 1.5em;
            color: $light-black;
        }

        .links{
            display: flex;

            a{  
                display: block;
                cursor: unset;

                img{
                    width: 32px;
                    height: 32px;
                    margin-right: 25px;
                }
            }
        }
    }
}

