@import "../variables";

.landing {
    height: 100vh;
    display: flex;
    align-items: center;
    position: relative;

    .content{
        padding: 0 50px;
    }

    .down-container {
        width: 100%;
        position: absolute;
        bottom: 30px;
        left: 0;
    }

    .down-container img {
        height: 30px;
        display: none;
        margin: 0 auto;

        @include breakpoint(desktopWidth) {
            display: block;
        }

        @include breakpoint(wideDesktopWidth) {
            width: 50px;
        }

        @include breakpoint(giantDesktopWidth) {
            display: none;
        }
    }

    .landing {
        padding: 0 50px;
        position: relative;
    }

    .name {
        font-size: 3em;
        color: $black;

        @include breakpoint(tabletWidth) {
            font-size: 4em
        }

        @include breakpoint(desktopWidth) {
            font-size: 5.5em
        }
    }

    .sub {
        font-family: "Kollektif";
        font-weight: 400;
        font-size: 1.25em;
        color: $light-black;

        @include breakpoint(desktopWidth) {
            font-size: 1.5em
        }
    }
}