@import './variables';

.app-container{
    position: relative;
}

.circle-container{
    pointer-events: none;
    z-index: 6;
    width: 100vw;
    height: 100vh;
    position: fixed;
    left: 0;
    top: 0;
}

#circle, #circle2{
    position: absolute;
    border-radius: 50%;
    will-change: transform, left, right, opacity;
}

#circle{
    transition: transform .5s ease, opacity .25s ease, left .05s ease-out, top .05s ease-out;
}

#circle2{
    transition: transform .5s ease, opacity .25s ease;
}

#circle{
    top: -100px;
    left: -100px;
    border: 1px solid $brown;
    height: 48px;
    width: 48px;
    transform: translate(-36.5%,-36.5%); 
}

#circle2{
    background-color: $light-green; 
    opacity: 0;
    height: 32px;
    width: 32px;
}

.link{
    cursor: url('../assets/images/mouse.svg'), auto;
    position: relative;
    z-index: 5;
}
  
.spaced {
    letter-spacing: .1em;
    text-transform: uppercase;
}